.contacts-images{
    position: absolute;
    display: flex;
    flex-direction: column;
    bottom: 0;
    position: fixed;
    align-items: center;
    margin-left: 40px;

    img{
        width: 30px;
        margin-bottom: 20px;
        height: 30px;
        cursor: pointer;
        transition: 0.2s ease-in-out;

        &:hover{
            transform: translateY(-15%);
        }
    }

    .white-border{
        width: 1px;
        height: 100px;
        background-color: white;
    }
}

@media screen and ( max-width: 1091px ) {
    .contacts-images{
        margin-left: 35px;
    }
}

@media screen and ( max-width: 768px) {
    .contacts-images{
        display: none;
    }
}